@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
    margin: 0;
    padding: 0;
    min-height: 100%;
    background: #222;
    font-family: 'Caveat', cursive;
}

main {
    padding: 20px;
    margin: auto;
    /* max-width: 1150px; */
    max-width: 90%;
}

#main-header {
    display: flex;
    justify-content: center;
    align-self: center;
    font-size: 60px;
    text-transform: uppercase;
    font-style: italic;
    color: var(--pokemon-yellow);
    margin-bottom: 50px;
    -webkit-text-fill-color: var(--pokemon-yellow);
    -webkit-text-stroke-color: #1a5276;
    -webkit-text-stroke-width: 1.25px;
}

#header-label {
    text-align: center;
    color: #fff;
    margin-bottom: 40px;
    margin-top: 0;
}

.spinner {
    text-align: center;
    color: white;
}

:root {
    --pokemon-yellow: #f4d03f;
    --poke-yellow: #f7dc6f;
    --box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
}

#app {
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(225px, 1fr));
    grid-auto-rows: minmax(300px, auto);
    grid-gap: 40px 40px;
    gap: 40px 40px;
    padding: 10px;
    justify-items: center;
    align-items: center;
}

.poke-card {
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
    border-top: 3px solid #f4d03f;
    border-top: 3px solid var(--pokemon-yellow);
    border-bottom: 3px solid #f4d03f;
    border-bottom: 3px solid var(--pokemon-yellow);
    overflow-x: hidden;
    overflow-y: hidden;
    padding: 10px;
    background: #333;
    border-radius: 10px;
    width: 100%;
    height: 100%;
    color: #eee;
    transition: 0.2s ease-in-out all;
}

.poke-card:hover {
    transform: scale(1.035);
    cursor: pointer;
}

.poke-card .card-id {
    display: flex;
    justify-content: center;
    align-self: flex-start;
    /* background: #222; */
    width: 50px;
    padding: 0;
    border-radius: 8px;
    color: #fff;
    font-size: 28px;
}

.flexy {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

#poke-hp {
    padding-right: 3px;
    color: red;
    font-size: 16px;
    padding-left: 5px;
}

.poke-card .flexy .card-hp {
    font-size: 1.5rem;
}

.poke-card .card-name {
    display: flex;
    justify-content: center;
    align-self: center;
    font-family: 'Caveat', cursive;
    font-size: 2rem;
}

.poke-card .card-image {
    display: block;
    width: 115px;
    height: 130px;
    margin: auto;
    margin-bottom: 10px;
}

.poke-card .card-details {
    color: #f4d03f;
    color: var(--pokemon-yellow);
    font-family: 'Caveat', cursive;
    font-size: 22px;
}

.poke-card span {
    color: #fff;
    font-family: 'Caveat', cursive;
    font-size: 1.2rem;
}

.pokiType {
    padding-right: 5px;
    color: #f4d03f !important;
    color: var(--pokemon-yellow) !important;
    font-family: 'Caveat', cursive;
    font-size: 22px !important;
}

.pokiBase {
    padding-right: 5px;
}

#search-bar {
    padding: 8px 8px;
    border-radius: 4px;
    font-size: 1.1rem;
    border: 1px solid #ddd;
}

#submit-btn {
    border-radius: 4px;
    margin: 5px;
    margin-left: 10px;
    background: #222;
    border: 1px solid #ddd;
    padding: 0;
}

#searchClick {
    display: inline-block;
    color: white;
    text-decoration: none;
    font-size: 0.9rem;
    width: 100%;
    padding: 6px 10px 6px 10px;
    border-radius: 4px;
}

#clear-search {
    display: block;
    cursor: pointer;
    border-radius: 4px;
    padding: 6px;
    background: #222;
    color: white;
    font-size: 0.9rem;
    border: 1px solid #ddd;
}

#searchClick:hover {
    background: #444;
}

#clear-search:hover {
    background: #444;
}

main #search-div {
    display: flex;
    justify-content: center;
    align-items: center;
}

@media screen and (max-width: 325px) {
    #search-bar {
        max-width: 150px;
    }
}

.wrap {
    text-align: center;
    color: white;
}

.wrap span {
    border: 1px solid var(--pokemon-yellow);
    padding: 20px;
}

